function FailureMessage(props) {
	return (
			<div class="error-message w-form-fail container w-container">
			<p>Sorry{props.name ? ` ${props.name}` : ""}, something went wrong while submitting the form.<br />Try again</p>
			</div>
	);
}
function summonCalendly(name, email) {
    Calendly.initPopupWidget(
        {
            url: 'https://calendly.com/jgoldfar/intro',
            prefill: {
                name: name,
                email: email
            }
        }
    );
    return false;
}
function SuccessMessage(props) {

	// <div class="w-widget-twitter-count-shim w--vertical" aria-hidden="true">
	// <div class="w-widget-twitter-count-inner">...</div>
	// </div>
	// <div class="w-widget-twitter-count-clear"></div><iframe title="Twitter Tweet Button" allowtransparency="true" frameborder="0" scrolling="no" src="https://platform.twitter.com/widgets/tweet_button.html" style="border: none; height: 20px; overflow: hidden; width: 73px;"></iframe>
	// </div>
	// <div class="share-button facebook w-widget w-widget-facebook"><iframe title="Facebook Like Button" src="https://www.facebook.com/plugins/like.php" scrolling="no" frameborder="0" allowtransparency="true" style="border: none; overflow: hidden; width: 55px; height: 65px;"></iframe></div>
    // <p class="referal-text">Help spread the word:</p>
    const nameparts = props.name ? props.name.split(' ') : ["",""];
    const thisname = ((nameparts.length == 2) && nameparts[0]) ? nameparts[0] : props.name;
	return (
        <Fragment>
		  <div class="success-message w-form-done container w-container">
			<p>Awesome, {thisname ? ` ${thisname}` : ""}! Thank you for your inquiry. We will get back to you as soon as possible!</p>
            <p>Schedule a call with us at your convenience <a href="#" onclick={() => {summonCalendly(props.name, props.email);}}>with Calendly</a></p>
          </div>
        </Fragment>
	);
}
function Header() {
    // <li>Have you struggled to achieve your commitments?</li>
    return (
        <Fragment>
		  <h1 class="heading">Desert Frog Solutions</h1>
		  <p class="subtitle">
            Partnering with visionaries, creators, entrepreneurs, and impactful organizations <br />
            Delivering IT and data solutions that enable success<br />
          </p>
		  <p class="subtitle questiontext">
            Our mission is to help professionals remain competitive and discover opportunities through adaptive, robust, and forward-looking solutions that fit their challenges.
          </p>
          <p class="subtitle questiontext">
			<ul>
              <li>Do you need innovative, reliable, and cost-effective results?</li>
			  <li>Do you want to understand and upgrade your systems?</li>
			  <li>Do you need resources and expertise to advance your vision?</li>
			</ul>
		  </p>
		  <p class="subtitle">You need to stand out<br /> We want to make it happen<br /></p>
        </Fragment>
    );
}
// globals/functions used in app interactivity
const timeToShowFailure = 3000;
function App() {
    const [userName, setUserName] = useState("");
    const [userEmail, setUserEmail] = useState("");
    const [success, setSuccess] = useState(false);
    const [failure, setFailure] = useState(false);
    const setName = (event) => {setUserName(event.target.value); console.log(userName);/* console.log(event); */};
    const setEmail = (event) => {setUserEmail(event.target.value); console.log(userEmail);};
    const submitForm = (event) => {
        // console.log(event);
        event.preventDefault();
        // const form = document.getElementById("email-form");
        const formData = new FormData(event.target);
        return fetch("https://usebasin.com/f/c5e3a00dbed9", {
            method: "POST",
            headers: {
                Accept: "application/json",
            },
            body: formData,
        })
        // return new Promise((resolve, reject) => {
        // //    // resolve({status: 400});
        //    resolve({status: 200});
        // //    // reject('an error occurred');
        // })
            .then((response) => {
                if (response.status === 200) {
                    setSuccess(true);
                } else {
                    setFailure(true);
                    setTimeout(() => {setFailure(false);}, timeToShowFailure);
                }
            })
            .catch((error) => {
                setFailure(true);
                setTimeout(() => {setFailure(false);}, timeToShowFailure);
            });
    };
    // ID for element being hydrated must be "preact_root"
    // https://github.com/preactjs/preact-cli/issues/1330
	return (
        <div id="preact_root">
          <div class="header-section wf-section">
			<div class="container w-container">
              <Header />
              {success ? <SuccessMessage name={userName} email={userEmail} /> : ""}
			  <div class="sign-up-form w-form" style={success ? 'display: none;' : ''}>
			    <form name="wf-form-signup-form" data-name="Signup Form" id="email-form" action="" method="post" enctype="multipart/form-data" class="w-clearfix" onSubmit={submitForm}>
			      <input type="text" class="field fieldfullwidth w-input" maxlength="256" name="Name" data-name="Name" placeholder="Enter your name (*)" id="Name" required="true" onChange={setName} />
			      <textarea placeholder="Share your project, product, or challenge. Or just say Hi!" maxlength="5000" id="Challenge" name="Challenge" required="" data-name="field" class="field fieldfullwidth fieldenterchallenge w-input"></textarea>
			      <input type="email" class="field w-input" maxlength="256" name="email" data-name="Email" placeholder="Enter your email address (*)" id="field" onChange={setEmail} required="true" />
			      <input type="submit" value="Contact Us" data-wait="Please wait..." class="button w-button" />
			    </form>
			  </div>
              {failure ? <FailureMessage name={userName} /> : ""}
            </div>
          </div>
		</div>
	);
}
